import React from "react";
export default  class InputAlert extends React.Component{
    constructor(){
        super()
        this.state={
            value:'',
            top:-20,
        }
        this.downslider()
    }
    downslider(){
        clearInterval(this.timer)
        this.timer = setInterval(() => {
            this.setState({
                top:this.state.top+1
            })
            if(this.state.top>=50){
                this.setState({
                    top:50
                })
                clearInterval(this.timer)
            }
        }, 1);
        
    }
    upslider(){
        clearInterval(this.timer)
        this.timer = setInterval(() => {
            this.setState({
                top:this.state.top - 2
            })
            if(this.state.top<=-20){
                this.setState({
                    top:-20
                })
                clearInterval(this.timer);
                this.props.setinputAlert(false)
            }
        }, 1);
    }
    componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    render(){
        var height= this.props.height;
        return  <div className="inputAlert-container" id="inputAlert-container" 
                    style={{width:`${height*1.775+10}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    onClick={()=>{
                        this.upslider()
                    }}
                >
                    <div className="inputAlert-child-container" style={{top:this.state.top+"px"}} >
                        {/* eslint-disable-next-line */}
                        <input autoFocus="autofocus" type={this.props.passwordType==1?'password': "text"} id ='inputAlert'
                            autoComplete="off"
                            onChange={(e)=>{
                                this.props.setPropsMoney(e.target.value)
                            }}
                            maxLength={`${this.props.maxLength}`}
                            value={this.props.money}
                            onBlur={()=>{
                                this.upslider()
                            }}
                            onKeyDown={(e)=>{
                                // eslint-disable-next-line
                                if(e.keyCode==13){
                                    this.upslider()
                                }
                            }}
                        />  
                    </div>
                </div>
    }
}