import React from "react";
import Recharge  from "../../container/Recharge";
import "./Dc.less";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import InputAlert from '../Alert/InputAlert';
function formatDuring(countDown) {
    let m =parseInt(Number(countDown)/60%60);
    let s =parseInt(Number(countDown)%60);
    return '00:'+m+':'+s
}
const api="http://111.231.57.144:8081/api"
export default class Dc_recharge extends React.Component{
    constructor(props){
        super(props);
        this.state={
            "money":'',
            "time":0,
            "showOrder":false,
            "true":false,
            "isAlert":"",
            "ApplyAlert":false
        }
        // 请求代充首页接口
       fetch(`${api}/replace_payment/index?user_id=${this.props.data.user_id}`,{
            method:"GET",
        }).then((data)=>data.json())
        .then((responseJson)=>{
            // eslint-disable-next-line 
            if(responseJson.status==0){
                this.setState({
                    results:responseJson
                })
            }
            var createdTime=Number(responseJson.data.created_at)
            let nowTime=Math.round(new Date().getTime()/1000);
            let countDown= 1800 -(nowTime-createdTime)
            this.setState({
                time:countDown
            })
        })
        .catch((error) => {
            alert(error)
        })
    }
    setPath(val){
        this.props.setProps(val)
    }
    componentDidMount(){
        //定时器
        this.timer = setInterval(()=>{
            this.setState({
                time:this.state.time-1
            })
        },1000)
        const {results} = this.state;
        if(!results){
            return null;
        }
    }
    componentWillUpdate(a,b){
        //关闭定时器
        // eslint-disable-next-line
        if(b.results && b.results.data.is_undone==0){
            clearInterval(this.timer)
        }
    }
    componentWillUnmount(){
        //关闭定时器
        clearInterval(this.timer)
        //避免异步请求结果未返回就切换页面报错
        this.setState = (state,callback)=>{
            return;
        };
    }
    onCopy(copy){
        this.setState({
            isAlertFZ:true,
            msg:"复制成功！ "+copy
        })
    }
    hideOrder(){
        this.setState({
            showOrder:false
        })
    }
    czBtn(){
        // eslint-disable-next-line 
        if(this.state.results.data.is_undone=='1'){ 
            this.setState({
                showOrder:true
            })
        }else{
            this.props.setMoney(this.state.money)
            this.props.setProps("#/recharge/list")
        }
    }
    setinputAlert(val){
        this.setState({
            inputAlert:val
        })
    }
    setPropsMoney(val){
        this.setState({
            money:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
        })
    }
    render(){
        const {user_id,nick_name}=this.props.data;
        const {results} = this.state;
        if(results){
            // 变量判断代充
            // eslint-disable-next-line 
            var {is_apply,is_replace,order_info} = results.data
        } else{
            // eslint-disable-next-line 
            var is_apply = 0;
            // eslint-disable-next-line 
            var is_replace = 0;
            // eslint-disable-next-line
            var order_info =""
        }
        const height =this.props.height;
        return  <Recharge current="1"  setPath={this.setPath.bind(this)}>
                <div className="dcBody">
                    <p className="dcP1">说明：充值金额必须与转账金额一致</p>
                    <div className="userInput publicinput">
                        <div> 
                            <input 
                                value={this.state.money}
                                onChange={()=>{

                                }}
                                onClick={()=>{
                                    this.setState({
                                        inputAlert:true,
                                    })
                                }}
                                placeholder='请输入充值金额'
                            />
                        </div>
                        <p className="deleBtn" onClick={()=>{
                            this.setState({
                                showP2:false,
                                money:''
                            })
                        }}>
                            <img src={process.env.PUBLIC_URL+"/images/75.png"} alt=""></img>
                        </p>
                    </div>
                    {/* 充值按钮 */}
                    <p style={{backgroundImage: "url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                         className="czBtn publicbtn" 
                         onClick={()=>{
                             // eslint-disable-next-line
                             if(this.state.money==''){
                                this.setState({
                                    isAlert:true,
                                    msg:'充值金额不能为空！'
                                })
                            }else if(Number(this.state.money)>100000){
                                this.setState({
                                    isAlert:true,
                                    msg:'单笔充值不能大于10万!'
                                })
                            }else{
                                this.czBtn()
                            }
                        }}
                    >
                        <span>确认充值</span>
                    </p>
                    <p className="dcP3" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/12.png)"}}>
                        如果充值后30分钟内未到账，请及时联系客服
                    </p>
                </div>
                {/* 判断是否当前账号是不是代充 */}
                {
                // eslint-disable-next-line 
                is_replace==1 ?
                    <p className="czBtn2 publicbtn" style={{backgroundImage: "url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                        onClick={()=>{
                            this.props.setProps("#/recharge/MyDc")
                        }}
                    >
                    <span>我的代充</span>
                    </p>
                    :<p className="czBtn2 publicbtn" style={{backgroundImage: "url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                        onClick={()=>{
                            // eslint-disable-next-line
                            if(is_apply==1){
                                this.setState({
                                    ApplyAlert:true
                                })
                            }else{
                                this.props.setProps("#/recharge/Apply_for")
                            }
                        }}
                    >
                    <span>成为代充</span>
                    </p>
                }   
                {/* 订单撤销弹窗 */}
                {
                    this.state.showOrder 
                    ? <div className="div-container" id="div-container" style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}>
                        <div className="div-child-container">
                            <div className="div-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                                <div  className="div-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                                    <span>订单撤销</span>
                                </div>
                            </div>
                            <div className="div-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onClick={()=>{
                                    this.hideOrder()
                                }}
                            ></div>
                            <div className="div-child">
                                <div className="div-child_icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/gantanhao.png)" }} ></div>
                                <div className="div-child_child1">
                                    
                                    您有一笔{parseInt(results.data.amount)}元的代理订单待完成，是否立即撤销?
                                </div>
                                <div className="div-child_child2"
                                >
                                    <span>自动撤单倒计时:</span>
                                    <span>{formatDuring(this.state.time)}</span>
                                </div>
                            </div>
                            <div className="div-btn">
                                <div className="publicbtn" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                    onClick={()=>{
                                        this.hideOrder()
                                    }}
                                >
                                取消
                                </div>
                                <div className="publicbtn"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                    onClick={()=>{
                                        this.hideOrder()
                                        // 取消订单接口,返回结果status
                                        fetch(`${api}/Order/cancelOrder?user_id=${user_id}&order_id=${results.data.order_id}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            this.setState({
                                                isAlert:true,
                                            })
                                            // eslint-disable-next-line
                                            if(responseJson.status==0){
                                                this.setState({
                                                    msg:"撤销成功！"
                                                })
                                            }else{
                                                this.setState({
                                                    msg:responseJson.msg
                                                })
                                            }
                                        })
                                        .catch((error) => {
                                            alert(error)
                                        })
                                    }}
                                    >
                                    确定
                                </div>
                            </div>
                            <div className="div-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)" }}></div>
                            </div>
                        </div>
                    :""
                }
                {/* 申请代充确认弹窗 */}
                {
                    this.state.ApplyAlert && !this.state.showOrder2 ? <div className="div-container" id="div-container" 
                        style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                        >
                        <div className="div-child-container">
                            <div className="div-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                                <div  className="div-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                                    <span>订单撤销</span>
                                </div>
                            </div>
                            <div className="div-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                    onTouchEnd={()=>{
                                        this.setState({
                                            ApplyAlert:false
                                        })
                                    }}
                            ></div>
                            <div className="div-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)" }}></div>
                            <div className="div-child" style={{paddingTop:'5%',boxSizing:"border-box"}}>
                                    <div className="div-child_icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/gantanhao.png)" }} ></div>
                                    <div className="div-child_child1"
                                        >
                                        您有一次申请代充记录待完成，是否前往支付?（选择撤销取消上次订单，重新填写资料）
                                    </div>
                            </div>
                            <div className="div-btn">
                                    <div className="publicbtn" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                        onTouchEnd={()=>{
                                            fetch(`${api}/replace_payment/cleanReplacePaymentInfo?user_id=${this.props.data.user_id}&order_id=${order_info.order_id}`,{
                                                method:"GET",
                                            }).then((data)=>data.json())
                                            .then((responseJson)=>{
                                                this.setState({
                                                    msg:responseJson.msg,
                                                    isAlert:true
                                                })
                                            })
                                            .catch((error) => {
                                                alert(error)
                                            })
                                            this.setState({
                                                ApplyAlert:false
                                            })
                                        }}
                                    > 撤销
                                    </div>
                                    <div className="publicbtn"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                        onClick={()=>{
                                            this.setState({
                                                ApplyAlert:false,
                                                showOrder2:true,
                                            })
                                        }}
                                    >确定
                                    </div>
                            </div>
                            
                        </div>
                    </div>
                    :""
                }
             {/* 弹窗 */}
             {
                    !this.state.isAlert?null:<div className="Alert-container" id="Alert-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    >
                    <div className="Alert-child-container" >
                        <div className="Alert-child">
                        {/* eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn">
                                <div
                                    onClick={()=>{
                                        this.setState({
                                            isAlert:false
                                        })
                                        fetch(`${api}/replace_payment/index?user_id=${this.props.data.user_id}`,{
                                            method:"GET",
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            this.setState({
                                                results:responseJson
                                            })
                                        })
                                        .catch((error) => {
                                            alert(error)
                                        })
                                    }}
                                >
                                    确定
                                </div>
                            </div>
                        </div>
                    </div>
            }
             {/* 复制结果弹窗 */}
             {
                    !this.state.isAlertFZ?null:<div className="Alert-container" id="Alert-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2),zIndex:"400"}}
                    >
                    <div className="Alert-child-container"  style={{height:'20%',top:'45%'}}>
                        <div className="Alert-child" style={{position:'absolute',top:'0',width:'90%',height:"65%"}}>
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn" style={{position:'absolute',bottom:'5%',left:'35%',width:'30%',height:"30%"}}>
                                <div style={{width:'100%',height:"100%",float:'left',background:'rgba(0, 152, 255, 255)'}} onClick={()=>{
                                    this.setState({
                                        isAlertFZ:false
                                    })
                                }}>确定</div>
                                
                            </div>
                        </div>
                    </div>
                }
            {/* 订单弹窗 */}
            {
                // eslint-disable-next-line 
                !this.state.showOrder2 || order_info==""?null : <div className="Applyfor-container" id="Applyfor-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                >
                <div className="Applyfor-child-container" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/kuangd2.png)"}}>
                    <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                        <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                            <span>订单信息</span>
                        </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        showOrder2:false
                                    })
                                }}
                    ></div>
                    <div className="Applyfor-child">
                            <div>
                                <span>充值方式：银行卡转账</span>
                                <span>收款银行：{order_info.bank_name}</span>
                            </div>
                            <div>用户昵称:{nick_name}</div>
                            <div>收款账号：{order_info.card_num}
                                        <CopyToClipboard text={order_info.card_num}
                                            onCopy={this.onCopy.bind(this)}
                                        >
                                         <div
                                            id="Orderfuzhi1"
                                            className="publicbtn"
                                            style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                            onTouchStart={()=>{
                                                document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                            }}
                                            onTouchEnd={()=>{
                                                document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                            }}
                                            >
                                            复制
                                        </div>
                                        </CopyToClipboard> 
                            </div>
                            <div>收款人姓名：{order_info.card_name}
                                        
                                        <CopyToClipboard text={order_info.card_name}
                                            onCopy={this.onCopy.bind(this)}
                                        >
                                            <div
                                                id="Orderfuzhi2"
                                                className="publicbtn"
                                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                onTouchStart={()=>{
                                                    document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                }}
                                                onTouchEnd={()=>{
                                                    document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                }}
                                            >
                                            复制
                                            </div>
                                        </CopyToClipboard> 
                            </div>
                            <div>转账金额:{Number(order_info.amount)}
                                    <CopyToClipboard text={Number(order_info.amount)}
                                            onCopy={this.onCopy.bind(this)}
                                        >
                                        <div
                                            id="Orderfuzhi3"
                                            className="publicbtn"
                                            style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                            onTouchStart={()=>{
                                                document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                            }}
                                            onTouchEnd={()=>{
                                                document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                            }}
                                        >
                                        复制
                                        </div>
                                    </CopyToClipboard> 
                            </div>
                            <div>请务必按照该金额（包含小数点）进行转账，否则无法正常上分</div>
                    </div>
                    <div className="Applyfor-child2">
                        <div>注意事项:</div>
                        <div>1.付款账户为随机账号，请以最新账号为准</div>
                        <div>2.请在30分钟内完成支付，超时系统将不予处理</div>
                        <div>3.完成转账后请在60分钟后查询您的账号余额，若未到账请联系在线客服</div>
                    </div>
                    <div className="Applyfor-btn1">
                            <div id="Applyfor_confrim"  
                                className="publicbtn"
                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                onClick={()=>{
                                    this.setState({
                                        showOrder2:false,
                                        showOrder3:true
                                    })
                                }}
                            >
                                <span>如何充值</span>
                            </div>
                    </div>
                    <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                    </div>
                </div> 
            }
            {/* 如何充值 */}
            {
                // eslint-disable-next-line 
                !this.state.showOrder3 ?null : <div className="Applyfor-container" id="Applyfor-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                >
                <div className="Applyfor-child-container">
                    <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                        <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                            <span>如何充值</span>
                        </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        showOrder3:false
                                    })
                                }}
                    ></div>
                    <div className="CZZY-child">
                        <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/zhiyin.jpg)"}}></div>
                    </div>
                   
                    <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                    </div>
                </div> 
            }
            {/* input弹窗 */}
            {
                !this.state.inputAlert?null:<InputAlert 
                    setinputAlert={this.setinputAlert.bind(this)}
                    setPropsMoney={this.setPropsMoney.bind(this)}
                    money={this.state.money}
                    height={this.props.height}
                    maxLength='6'
                    ></InputAlert>
            }
        </Recharge>
    }
}
