import React from "react";
import "./Apply.less";
import Recharge from "../../container/Recharge";
import "./MyDc.less";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import InputAlert from "../Alert/InputAlert";
const api="http://111.231.57.144:8081/api";
export default class MyDc extends React.Component{
    constructor(props){
        super(props);
        this.state={
            "showValue":false,
            "showScale":false,
            "scale":"",
            "value":"",
            "details":"",
            "showOrder":false,
            "isShowdetails":false,
            "disabled":false,
        }
        fetch(`${api}/replace_payment/myInfo?user_id=${this.props.data.user_id}`,{
            method:"GET"
        }).then((data)=>data.json())
        .then((responseJson)=>{
            this.setState({
                results:responseJson,
                details:responseJson.data.details
            })
        })
        .catch((error) => {
            alert(error)
        })
    }
    setPath(val){
        this.props.setProps(val)
    }
    //显示增加金额弹窗
    showValue() {
        this.setState({ showValue: true })
    }
    //隐藏增加金额弹窗
    hideValue() {
        this.setState({ showValue: false })
    }
     //显示修改比例弹窗
    showOrder() {
        this.setState({ showOrder: true })
    }
    //隐藏修改比例弹窗
    hideOrder() {
        this.setState({ showOrder: false })
    }
    //显示详情弹窗
    showDetailes(){
        this.setState({isShowdetails:true})
    }
    //隐藏详情弹窗
    hideDetailes(){
        this.setState({isShowdetails:false})
    }
    //避免异步请求结果未返回就切换页面报错
    componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    componentWillReceiveProps(a,b){
          // eslint-disable-next-line 
        if (a.homeModel.orderStatus.status == 0) {
            this.hideValue()
            this.showOrder()
        }
          // eslint-disable-next-line 
        if(a.homeModel.statusInfo.status ==0){
            this.hideScale()
        }
    }
    onCopy(copy){
        this.setState({
            isAlertFZ:true,
            msg:"复制成功！ "+copy
        })
    }
    setinputAlert(val){
        this.setState({
            inputAlert:val
        })
    }
    setPropsMoney(val){
        // eslint-disable-next-line
        if(this.state.inputkeys=='details'){
            this.setState({
                details:val
            })
        // eslint-disable-next-line
        }else if(this.state.inputkeys=='scale'){
            this.setState({
                scale:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        // eslint-disable-next-line
        }else if(this.state.inputkeys=='value'){
            this.setState({
                value:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        }
    }
    render(){
        const{user_id,nick_name,client,proxy_id,proxy_name,from_brand} =this.props.data;
        const {results,orderStatus} =this.state;
        if(results){
            var available_amount=results.data.available_amount?parseInt(results.data.available_amount):0;
            var min_amount=Number(results.data.min_amount)
            var max_amount=Number(results.data.max_amount)
        }else{
            // eslint-disable-next-line 
            var min_amount=5000
            // eslint-disable-next-line 
            var max_amount=50000
        }
        // 弹窗固定高度
        const height =this.props.height;
        return  <Recharge current="1" setPath={this.setPath.bind(this)}>
            <div className="MyDctitle">
                <div onTouchEnd={()=>{
                        this.props.setProps("#/")
                    }}
                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/fanhui1.png)"}} 
                ></div>
                <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}}  >
                    <div>
                        我的代充
                    </div>
                </div>
                <div onTouchEnd={()=>{
                        this.props.setProps("#/recharge/MyOrder")
                    }}
                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}} 
                ><div>我的订单</div></div>
            </div>
            <div className="MyDcBgWhite">
                <div className="MyDcBgWhite-child1">
                    <span className="WordArt">当前代充余额：</span>
                    <span>{available_amount}</span>
                    <span onClick={()=>{
                            this.showValue()
                        }}>追加余额
                    </span>
                    <span onClick={()=>{
                            this.props.setProps("#/recharge/BuyScoreHistory")
                        }}
                    >购分历史
                    </span>
                </div>
                <div className="MyDcBgWhite-child2">
                    <span className="WordArt">买分详情:</span>
                    <div className="MyDcBgWhite-child2-child2" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/neikuagn.png)"}}>
                        <div>{this.state.details}
                        </div>
                    </div>
                    <div className="MyDcBgWhite-child2-child3"  >
                        <div 
                            style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                            onClick={()=>{
                                this.setState({
                                    disabled:true
                                })
                                
                        }}><input 
                                className='publicbtn'
                                 value='修 改'
                                 onChange={()=>{
                                 }}
                                 onClick={()=>{
                                     this.setState({
                                        inputAlert:true,
                                        inputkeys:"details"
                                     })
                                 }}
                            /></div>
                        <div
                            className='publicbtn'
                            style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                            onClick={()=>{
                                // eslint-disable-next-line 
                              if(this.state.details!=""){
                                  fetch(`${api}/replace_payment/saveDetails?user_id=${user_id}&details=${this.state.details}`,{
                                      method:"GET"
                                  }).then((data)=>data.json())
                                  .then((responseJson)=>{
                                      // eslint-disable-next-line
                                      if(responseJson.status==0){
                                          this.setState({
                                              isAlert:true,
                                              msg:"提交修改成功！"
                                          })
                                      }else{
                                          this.setState({
                                              isAlert:true,
                                              msg:responseJson.msg
                                          })
                                      }
                                  })
                                  .catch((error) => {
                                      alert(error)
                                  })
                                  this.hideDetailes()
                                  this.setState({
                                    disabled:false
                                  })
                              }else{
                                  this.setState({
                                      isAlert:true,
                                      msg:'详情不能为空！'
                                  })
                              }
                          }}
                        >提 交</div>
                    </div>
                </div>
            </div> 
               {/* 追加金额弹窗 */}
               {
                   !this.state.showValue?null :<div className="div-container" id="div-container"  
                   style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                   >
                   <div className="div-child-container" >
                        <div className="div-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                            <div  className="div-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                                <span>代充买分</span>
                            </div>
                        </div>
                        <div className="div-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onClick={()=>{
                                    this.setState({
                                        showValue:false
                                    })
                                }}
                        ></div>
                        <div className="div-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)" }}></div>
                       <div className="MyDcBzj-child">
                                <div className="MyDcBzj-child_icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/gantanhao.png)" }} ></div>
                               <div className="MyDcBzj-child-child2">
                                    <div className="WordArt" >
                                    买分比例:
                                    </div>
                                    <div className='publicinput'>
                                            <input type="text"
                                                onClick={()=>{
                                                    this.setState({
                                                        inputAlert:true,
                                                        inputkeys:'scale'
                                                    })
                                                }}
                                                onChange={(e)=>{
                                                }}
                                                value={this.state.scale}
                                            />
                                            <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                                                onClick={()=>{
                                                    this.setState({
                                                        scale :''
                                                    })
                                                }} 
                                            ></i>
                                    </div>
                                    <span>%</span>
                                    
                               </div>
                               <div className="MyDcBzj-child-child3" >
                                    <div className="WordArt" >
                                    买分金额:
                                    </div>
                                    <div className='publicinput'>
                                            <input type="text"
                                                onClick={()=>{
                                                    this.setState({
                                                        inputAlert:true,
                                                        inputkeys:'value'
                                                    })
                                                }}
                                                onChange={(e)=>{
                                                }}
                                                value={this.state.value}
                                            />
                                            <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                                                onClick={()=>{
                                                    this.setState({
                                                        value :''
                                                    })
                                                }} ></i>
                                    </div>
                                    <span></span>
                               </div>
                               <div className="MyDcBzj-child-child4">
                                    <span>买分成功后，您可增加额度为：</span>
                                    <span>{this.state.scale*this.state.value/100}</span>
                                </div>
                       </div>
                       <div className="div-btn" style={{top:'17%'}}>
                               {/* 创建订单 */}
                               <div className="publicbtn"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)",marginLeft:'35%'}}
                                    onClick={()=>{
                                        //发送请求
                                          // eslint-disable-next-line 
                                        if(this.state.value=='' || this.state.scale == ''){
                                            this.setState({
                                                isAlert:true,
                                                msg:"输入不能为空！"
                                            })
                                        }else if(this.state.scale>102){
                                            this.setState({
                                                isAlert:true,
                                                msg:"大于系统最大买分比例（102）！"
                                            })
                                        }else if(this.state.value<min_amount){
                                            this.setState({
                                                isAlert:true,
                                                msg:`小于系统单笔最小买分金额（${min_amount}）！`
                                            })
                                        }else if(this.state.value>max_amount){
                                            this.setState({
                                                isAlert:true,
                                                msg:`大于系统单笔最大买分金额（${max_amount}）！`
                                            })
                                        }else{
                                            fetch(`${api}/Payment/transferPayment?user_id=${user_id}&nick_name=${nick_name}&fraction_scale=${this.state.scale}&amount=${this.state.value}&client=${client}&proxy_id=${proxy_id}&proxy_name=${proxy_name}&from_brand=${from_brand}&order_type=2`,{
                                                method:"GET"
                                            }).then((data)=>data.json())
                                            .then((responseJson)=>{
                                                this.setState({
                                                    orderStatus:responseJson, 
                                                })
                                                // eslint-disable-next-line 
                                                if(responseJson.status==0){
                                                    this.setState({
                                                        showOrder:true, 
                                                    })
                                                }else{
                                                    this.setState({
                                                        isAlert:true, 
                                                        msg:responseJson.msg+"请输入其他金额"
                                                    })
                                                }
                                                this.hideValue()
                                            })
                                            .catch((error) =>{
                                                alert(error)
                                            })
                                        } 
                                    }}
                               >提交</div>
                       </div>
                   </div>
               </div>   
               }
               {/* 订单弹窗 */}
               {
                    // eslint-disable-next-line
                    !this.state.showOrder || orderStatus.status!=0 ?null : <div className="Applyfor-container" id="Applyfor-container" 
                        style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    >
                    <div className="Applyfor-child-container">
                        <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                            <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                                <span>创建订单</span>
                            </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        showOrder:false
                                    })
                                }}
                        ></div>
                        <div className="Applyfor-child">
                                   <div>
                                       <span>充值方式：银行卡转账</span>
                                       <span>收款银行：{orderStatus.data.bank_name}</span>
                                   </div>
                                   <div>用户昵称：{nick_name}</div>
                                   <div>收款账号：{orderStatus.data.card_num}
                                        <CopyToClipboard text={orderStatus.data.card_num}
                                            onCopy={this.onCopy.bind(this)}
                                        >
                                        <div
                                            className="publicbtn"
                                            id="Orderfuzhi1"
                                            style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                            onTouchStart={()=>{
                                                document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                            }}
                                            onTouchEnd={()=>{
                                                document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                            }}
                                        >
                                        复制
                                    </div>
                                        </CopyToClipboard>
                                            
                                   </div>
                                   <div>收款人姓名：{orderStatus.data.card_name}
                                        <CopyToClipboard text={orderStatus.data.card_name}
                                            onCopy={this.onCopy.bind(this)}
                                        >
                                           <div
                                                className="publicbtn"
                                                id="Orderfuzhi2"
                                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                onTouchStart={()=>{
                                                    document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                }}
                                                onTouchEnd={()=>{
                                                    document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                }}
                                            >
                                                复制
                                            </div>
                                        </CopyToClipboard>
                                            
                                        
                                   </div>
                                   <div>转账金额:{Number(orderStatus.data.amount)}
                                        <CopyToClipboard text={Number(orderStatus.data.amount)}
                                            onCopy={this.onCopy.bind(this)}
                                        >
                                            <div
                                                className="publicbtn"
                                                id="Orderfuzhi3"
                                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                onTouchStart={()=>{
                                                    document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                }}
                                                onTouchEnd={()=>{
                                                    document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                }}
                                            >
                                                复制
                                            </div>
                                        </CopyToClipboard>
                                   </div>
                                   <div>请务必按照该金额（包含小数点）进行转账，否则无法正常上分</div>
                        </div>
                        <div className="Applyfor-child2">
                            <div>注意事项:</div>
                            <div>1.付款账户为随机账号，请以最新账号为准</div>
                            <div>2.请在30分钟内完成支付，超时系统将不予处理</div>
                            <div>3.完成转账后请在60分钟后查询您的账号余额，若未到账请联系在线客服</div>
                        </div>
                        <div className="Applyfor-btn1">
                                <div id="Applyfor_confrim" 
                                  className="publicbtn"
                                  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                    onClick={()=>{
                                        this.setState({
                                            showOrder3:true
                                        })
                                        //重新拉取数据
                                        fetch(`${api}/replace_payment/myInfo?user_id=${this.props.data.user_id}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            this.setState({
                                                results:responseJson
                                            })
                                        })
                                        .catch((error) => {
                                            alert(error)
                                        })
                                        this.setState({
                                            isAlert:false,
                                            scale:'',
                                            value:''
                                        })
                                        this.hideOrder()
                                    }}
                                >
                                    <span>如何充值</span>
                                </div>
                            </div>
                        <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                        </div>
                    </div> 
                }
               {/* 提交修改金额结果 */}
                 {
                    !this.state.isAlert?null:<div className="Alert-container" id="Alert-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    >
                    <div className="Alert-child-container" >
                        <div className="Alert-child">
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn">
                                <div
                                    onClick={()=>{
                                        //重新拉取数据
                                        fetch(`${api}/replace_payment/myInfo?user_id=${this.props.data.user_id}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            this.setState({
                                                results:responseJson
                                            })
                                        })
                                        .catch((error) => {
                                            alert(error)
                                        })
                                        this.setState({
                                            isAlert:false
                                        })
                                    }}
                                >
                                    确定
                                </div>
                            </div>
                        </div>
                    </div>
                }
                {/* 复制结果弹窗 */}
                {
                    !this.state.isAlertFZ?null:<div className="Alert-container" id="Alert-container"
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2),zIndex:'400'}}
                    >
                    <div className="Alert-child-container"  style={{height:'20%',top:'45%'}}>
                        <div className="Alert-child" style={{position:'absolute',top:'0',width:'90%',height:"65%"}}>
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn" style={{position:'absolute',bottom:'5%',left:'35%',width:'30%',height:"30%"}}>
                                <div style={{width:'100%',height:"100%",float:'left',background:'rgba(0, 152, 255, 255)'}} onClick={()=>{
                                    this.setState({
                                        isAlertFZ:false
                                    })
                                }}>确定</div>
                                
                            </div>
                        </div>
                    </div>
                }
                 {/* 如何充值 */}
                 {
                // eslint-disable-next-line 
                !this.state.showOrder3 ?null : <div className="Applyfor-container" id="Applyfor-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                >
                <div className="Applyfor-child-container" >
                    <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                        <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                            <span>如何充值</span>
                        </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        showOrder3:false
                                    })
                                }}
                    ></div>
                    <div className="CZZY-child">
                        <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/zhiyin.jpg)"}}></div>
                    </div>
                   
                    <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                    </div>
                </div> 
                }
                 {/* input弹窗 */}
                 {
                !this.state.inputAlert?null:<InputAlert 
                    setinputAlert={this.setinputAlert.bind(this)}
                    setPropsMoney={this.setPropsMoney.bind(this)}
                    // eslint-disable-next-line
                    money={this.state.inputkeys=='details'?this.state.details:(this.state.inputkeys=='scale'?this.state.scale:this.state.value)}
                    height={this.props.height}
                    // eslint-disable-next-line
                    maxLength={this.state.inputkeys=='details'?'100':(this.state.inputkeys=='scale'?'3':'6')}
                    ></InputAlert>
                }
        </Recharge>
    }
}