import React from "react";
import WithDrawCash from "../../container/WithDrawCash";
import "./Present.less";
import {toggleSlide,timestampToTime} from "../Alert/config";
import InputAlert from "../Alert/InputAlert";
import './PresentHistory.less';
// const api="http://111.231.57.144:8081/api"
const api="http://10.63.252.30:8087/api";
export default class PresentHistory extends React.Component{
    constructor(props){
        super(props);
        this.state={
            page:1,
            "isShow":false,
            presentID:"",
            order_id:'',
            status:0,
        }
        fetch(`${api}/give/myGiveList?user_id=${this.props.data.user_id}&type=2&status=${this.state.status}&page=${this.state.page}&page_set=5&id=${this.state.presentID}`,{
            method:"GET"
        }).then((data)=>data.json())
        .then((responseJson)=>{
            console.log(responseJson)
            // eslint-disable-next-line
            if(responseJson.status==0){
                this.setState({
                    results:responseJson,
                    myOrderList:responseJson.data.list
                })
            }else{
                this.setState({
                    myOrderList:[]
                })
            }
           
        })
        .catch((error) =>{
            alert(error)
        })
    }
    setPath(val){
        this.props.setProps(val)
    }
    showAlert(value){
        this.setState({
            isShow:true
        })
    }
    hideAlert(){
        this.setState({
            isShow:false
        })
    }
    //避免异步请求结果未返回就切换页面报错
    componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    setinputAlert(val){
        this.setState({
            inputAlert:val
        })
    }
    setPropsMoney(val){
        // eslint-disable-next-line
        if(this.state.inputkeys=='presentID'){
            this.setState({
                presentID:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        }
    }
    render(){
        const{user_id} =this.props.data;
        var {results,myOrderList}=this.state;
        if(results){
            // eslint-disable-next-line 
            var totalPage = results.data.total_page ==0?1:results.data.total_page;
        }else{
            // eslint-disable-next-line 
            var totalPage = 1;
        }
        const height =this.props.height;
        var dataList =[
            "全部",
            "赠送中",
            "已赠送",
        ]
        return  <WithDrawCash current="2" setPath={this.setPath.bind(this)}>
            <div className="MyOrdertitle">
                <div onTouchEnd={()=>{
                        this.props.setProps("#/withdrawCash/give")
                    }}
                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/fanhui1.png)"}} 
                ></div>
                <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}}
                    onTouchEnd={()=>{
                        this.props.setProps("#/withdrawCash/presenthistory")
                    }}
                ><div>赠送记录</div></div>
                <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}}><div>受赠记录</div></div>
            </div>
            <div className="MyOrderBgWhite" >
                <div className="MyOrderBgWhite_child1">
                    <div className="MyOrderBgWhite_child1_child1 publicinput">
                        <input type="text" placeholder="输入ID"
                            onChange={(e)=>{
                            }}
                            onClick={()=>{
                                this.setState({
                                    inputAlert:true,
                                    inputkeys:'presentID'
                                })
                            }}
                            value={this.state.presentID}
                        />
                         <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                            onClick={()=>{
                                this.setState({
                                    presentID :''
                                })
                            }} 
                        ></i>
                    </div>
                    <div  className="MyOrderBgWhite_child1_select publicinput">
                                <form action="" method="post" > 
                                    <div id="divselect" > 
                                        <cite id="cite"
                                               onClick={()=>{
                                                toggleSlide('selectUl','500','120')
                                            }}
                                        >赠送中</cite> 
                                        <ul id="selectUl" style={{display:"none"}} > 
                                        {
                                            dataList.map((item,index)=>{
                                                return  <li key={index}
                                                    onClick={()=>{
                                                        document.getElementById("selectUl").style.display="none"
                                                        document.getElementById("cite").innerHTML=item;
                                                        this.setState({
                                                            status:index
                                                        })
                                                    }}
                                                    // eslint-disable-next-line 
                                                ><a href="javascript:;" selectid={index+1}>{item}</a></li>
                                            })
                                        }
                                        </ul> 
                                        
                                    </div> 
                                    <input name="" type="hidden" value="" id="inputselect"/> 
                                </form> 
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/1.png)"}}></i>
                            </div>
                    <div
                        className="MyOrderBgWhite_child1_child3"
                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/sousuo.png)"}}
                        onClick={()=>{
                            this.setState({
                                page:1
                            })
                            fetch(`${api}/give/myGiveList?user_id=${this.props.data.user_id}&type=2&status=${this.state.status}&page=${this.state.page}&page_set=5&id=${this.state.presentID}`,{
                                method:"GET"
                            }).then((data)=>data.json())
                            .then((responseJson)=>{
                                // eslint-disable-next-line
                                if(responseJson.status==0){
                                    this.setState({
                                        results:responseJson,
                                        myOrderList:responseJson.data.list
                                    })
                                }else{
                                    this.setState({
                                        myOrderList:[]
                                    })
                                }
                               
                            })
                            .catch((error) =>{
                                alert(error)
                            })
                        }}
                    ></div>
                </div>
                <div className="PresentHistory_child2" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/titlebg.png)"}}>
                    <span >单号</span>
                    <span >赠送人ID</span>
                    <span >昵称</span>
                    <span >赠送金额</span>
                    <span >状态</span>
                    <span >创建时间</span>
                </div>
                {/* 动态展示列表 */}
                <div 
                    className="PresentHistory_child3"
                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5ge.png)"}}
                    >
                    {
                        // eslint-disable-next-line
                        myOrderList==undefined?"":myOrderList.map((item,index)=>{
                            return <div className="PresentHistoryList" key={index}
                                    >
                                        <div>{item.give_no.substring(item.give_no.length-6)}</div>
                                        <div>{item.user_id}</div>
                                        <div>{item.user_name}</div>
                                        <div>{Math.floor(item.amount*100)/100}</div>
                                        {/* eslint-disable-next-line  */}
                                        <div>{item.status==1?'赠送中':'已赠送'}</div>
                                        <div>{timestampToTime(item.created_at)}</div>
                                </div>
                                
                        })
                    }
                </div>
                <div className="MyOrderBgWhite_child4">
                    {/* 上一页 */}
                    <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/shangyiye.png)"}}
                           onClick={()=>{
                            if(this.state.page >1){
                                this.setState({
                                    page:this.state.page-1
                                })
                                fetch(`${api}/give/myGiveList?user_id=${this.props.data.user_id}&type=2&status=${this.state.status}&page=${this.state.page-1}&page_set=5&id=${this.state.presentID}`,{
                                    method:"GET"
                                }).then((data)=>data.json())
                                .then((responseJson)=>{
                                    // eslint-disable-next-line
                                    if(responseJson.status==0){
                                        this.setState({
                                            results:responseJson,
                                            myOrderList:responseJson.data.list
                                        })
                                    }else{
                                        this.setState({
                                            myOrderList:[]
                                        })
                                    }
                                   
                                })
                                .catch((error) =>{
                                    alert(error)
                                })
                            }else{
                                return null
                            }
                        }}
                    ></div>
                    {/* eslint-disable-next-line  */}
                    <div>{this.state.page}/{totalPage==0?1:totalPage}</div>
                    {/* 下一页 */}
                    <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/xiayiye.png)"}}
                        onClick={()=>{
                            if(this.state.page<totalPage){
                                this.setState({
                                    page:this.state.page+1
                                })
                                fetch(`${api}/give/myGiveList?user_id=${this.props.data.user_id}&type=2&status=${this.state.status}&page=${this.state.page+1}&page_set=5&id=${this.state.presentID}`,{
                                    method:"GET"
                                }).then((data)=>data.json())
                                .then((responseJson)=>{
                                    // eslint-disable-next-line
                                    if(responseJson.status==0){
                                        this.setState({
                                            results:responseJson,
                                            myOrderList:responseJson.data.list
                                        })
                                    }else{
                                        this.setState({
                                            myOrderList:[]
                                        })
                                    }
                                   
                                })
                                .catch((error) =>{
                                    alert(error)
                                })
                            }else{
                                return null
                            }
                        }}
                    ></div>
                </div>
            </div> 
            {/* 结果弹窗 */}
             {
                    !this.state.isAlert?null:<div className="Alert-container" id="Alert-container"  
                        style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                     >
                    <div className="Alert-child-container" >
                        <div className="Alert-child ">
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn">
                                <div
                                    onClick={()=>{
                                        //重新拉取数据
                                        fetch(`${api}/give/myGiveList?user_id=${this.props.data.user_id}&type=2&status=${this.state.status}&page=${this.state.page}&page_set=5&id=${this.state.presentID}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line
                                            if(responseJson.status==0){
                                                this.setState({
                                                    results:responseJson,
                                                    myOrderList:responseJson.data.list
                                                })
                                            }else{
                                                this.setState({
                                                    myOrderList:[]
                                                })
                                            }
                                           
                                        })
                                        .catch((error) =>{
                                            alert(error)
                                        })
                                        this.setState({
                                            isAlert:false,
                                        })
                                    }}
                                >
                                    确定
                                </div>
                            </div>
                        </div>
                    </div>
                }
                {/* 确定赠送弹窗 */}
             {
                    !this.state.isAlert2?null:<div className="MyorderAlert-container" id="MyorderAlert-container"   
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    >
                    <div className="MyorderAlert-child-container"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/alertbg.jpg)",top:'30%'}} >
                        <div className="MyorderAlert-child" >
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="MyorderAlert-btn" >
                                <div 
                                    className='publicbtn'
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                                    onClick={()=>{
                                    this.setState({
                                        isAlert2:false
                                    })
                                }}>否</div>
                                <div
                                    className='publicbtn'
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                                    onClick={()=>{
                                        //发送请求
                                        fetch(`${api}/give/confirmGiveAmount?user_id=${user_id}&give_no=${this.state.give_no}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line
                                            if(responseJson.status==0){
                                                this.setState({
                                                    isAlert:true,
                                                    msg:"确认成功!"
                                                })
                                            }else{
                                                this.setState({
                                                    isAlert:true,
                                                    msg:responseJson.msg
                                                })
                                            }
                                            this.hideAlert();
                                            this.setState({
                                                isAlert2:false
                                            })
                                        })
                                        .catch((error) =>{
                                            alert(error)
                                        })
                                    }}
                                >
                                    是
                                </div>
                            </div>
                        </div>
                    </div>
                }
                {/* input弹窗 */}
                {
                    !this.state.inputAlert?null:<InputAlert 
                        setinputAlert={this.setinputAlert.bind(this)}
                        setPropsMoney={this.setPropsMoney.bind(this)}
                        // eslint-disable-next-line
                        money={this.state.presentID}
                        height={this.props.height}
                        // eslint-disable-next-line
                        maxLength='10'
                        ></InputAlert>
                }
        </WithDrawCash>
        
    }
}
