import React from "react";
import "./Apply.less";
import Recharge from "../../container/Recharge";
import {toggleSlide} from "../Alert/config";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import InputAlert from '../Alert/InputAlert';
const api="http://111.231.57.144:8081/api"
// const api="http://10.63.252.30:8087/api"
export default  class Apply_for extends React.Component{
    constructor(props){
        super(props);
        this.state={
            "statusInfo":false,
            "saveDetailsResults":"",
            "proportion":'',
            "buyAmount":'',
            "show":false,
            "contact_type":1,
            "contact_info":"",
            "details":"",
            "isAlert":false,
            "isAlert2":false,
            "isAlert3":false,
            "msg":""
        }
        fetch(`${api}/replace_payment/register?user_id=${this.props.data.user_id}`,{
            method:"GET"
        }).then((data)=>data.json())
        .then((responseJson)=>{
            // eslint-disable-next-line
            if(responseJson.status==0){
                this.setState({
                    results:responseJson,
                    contact_info:responseJson.data.contact_info,
                    // eslint-disable-next-line
                    contact_type:responseJson.data.contact_type == 0? '1':responseJson.data.contact_type,
                    details:responseJson.data.details,
                })
            }
        })
        .catch((error) => {
            alert(error)
        })
    }
    setPath(val){
        this.props.setProps(val)
    }
    //显示订单弹窗
    showOrder() {
        this.setState({ show: true })
    }
    //隐藏订单弹窗
    hideOrder() {
        this.setState({ show: false })
    }
    componentWillReceiveProps(a,b){
         //判断创建订单的返回结果
        if (b.orderStatus.status === "0") {
            this.showOrder()
        }else if(b.orderStatus.status === "-1"){
            this.setState({
                isAlert3:true
            })
        }
    }
    onCopy(copy){
        this.setState({
            isAlertFZ:true,
            msg:"复制成功！ "+copy
        })
    }
    setinputAlert(val){
        this.setState({
            inputAlert:val
        })
    }
    setPropsMoney(val){
        // eslint-disable-next-line
        if(this.state.inputkeys=='contact_info'){
            this.setState({
                contact_info:val.replace(/[\u4E00-\u9FA5]/g,'')
            })
        // eslint-disable-next-line
        }else if(this.state.inputkeys=='details'){
            this.setState({
                details:val
            })
        // eslint-disable-next-line
        }else if(this.state.inputkeys=='proportion'){
            this.setState({
                proportion:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        // eslint-disable-next-line
        }else if(this.state.inputkeys=='buyAmount'){
            this.setState({
                buyAmount:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        }
    }
    render(){
        const{user_id,nick_name,client,proxy_id,proxy_name,from_brand} =this.props.data;
        const{results,contact_info,contact_type,details,orderStatus,} =this.state;
        if(results){
            var max= results.data.max_scale*100;
            var min_amount = Number(results.data.min_amount);
            var max_amount = Number(results.data.max_amount);
            //判断是显示确定还是修改基本信息
            var showInfo = results.data.contact_info
        }else{
            // eslint-disable-next-line
            var showInfo=''
        }
        // const height= window.screen.height;
        const height =this.props.height;
        var dataList =[
            "微信",
            "QQ"
        ];
        return  <Recharge   current="1" setPath={this.setPath.bind(this)}>
                 <div className="MyDctitle">
                    <div onTouchEnd={()=>{
                            this.props.setProps("#/")
                        }}
                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/fanhui1.png)"}} 
                    ></div>
                    <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}}  ><div>申请代充</div></div>
                    
                </div>
                    <div  className="huadongBg">
                        <div className="whiteBg1">
                            <div className="whiteBg1_child1 WordArt">
                                基础信息:
                            </div>
                            <p className="whiteBg1_child2">昵称: <span>{nick_name}</span></p>
                            <p className="whiteBg1_child3" >ID: <span>{user_id}</span></p>
                            <div className="whiteBg1_child4" >
                                <div  className="whiteBg1_child4_select publicinput" >
                                        <form action="" method="post" > 
                                            <div id="divselect" > 
                                                <cite id="cite"
                                                    onClick={()=>{
                                                        toggleSlide('selectUl','500','40')
                                                    }}
                                                    // eslint-disable-next-line
                                                >{contact_type==1?"微信":"QQ"}</cite> 
                                                <ul id="selectUl" style={{display:"none"}} > 
                                                {
                                                    dataList.map((item,index)=>{
                                                        return  <li key={index}
                                                            onClick={()=>{
                                                                document.getElementById("selectUl").style.display="none";
                                                                document.getElementById("cite").innerHTML=item;
                                                                this.setState({
                                                                    current:index+1,
                                                                    contact_type:index+1
                                                                })
                                                            }}
                                                            // eslint-disable-next-line
                                                        ><a href="javascript:;" selectid={index+1}>{item}</a></li>
                                                    })
                                                }
                                                </ul> 
                                                
                                            </div> 
                                            <input name="" type="hidden" value="" id="inputselect" maxLength="20"/> 
                                        </form> 
                                        <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/1.png)"}}></i>
                                    </div>
                                
                                <div className="whiteBg1_child4_child2 publicinput">
                                    <i>*</i>
                                    <input type="text"
                                        placeholder="请输入账户"
                                        onChange={(e)=>{
                                        }}
                                        onClick={()=>{
                                            this.setState({
                                                inputAlert:true,
                                                inputkeys:'contact_info'
                                            })
                                        }}
                                        value={contact_info}
                                    />
                                     <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                                        onClick={()=>{
                                            this.setState({
                                                contact_info :''
                                            })
                                        }} 
                                    ></div>
                                </div>
                                {/* 提交基本资料 */}
                            {
                                    // eslint-disable-next-line
                                    showInfo==''?<div  className="whiteBg1_child4_child3 publicbtn"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                        onClick={()=>{
                                            //
                                            if(this.state.contact_info.length>=1) {
                                                fetch(`${api}/Replace_payment/saveContact?user_id=${user_id}&nick_name=${nick_name}&contact_type=${this.state.contact_type}&contact_info=${this.state.contact_info}&client=${client}&proxy_id=${proxy_id}&proxy_name=${proxy_name}&from_brand=${from_brand}&action=add`,{
                                                    method:"GET"
                                                }).then((data)=>data.json())
                                                .then((responseJson)=>{
                                                    // eslint-disable-next-line
                                                    if(responseJson.status!=0){
                                                        this.setState({
                                                            isAlert:true,
                                                            msg:"提交失败!"
                                                        })
                                                    }else{
                                                        this.setState({
                                                            statusInfo:responseJson,
                                                            isAlert:true,
                                                            msg:"提交成功！"
                                                        })
                                                    }
                                                })
                                                .catch((error) => {
                                                    alert(error)
                                                })
                                               // eslint-disable-next-line
                                            }else if(contact_info==''){
                                                this.setState({
                                                    isAlert:true,
                                                    msg:"提交信息不能为空！"
                                                })
                                            }
                                        }}
                                        >
                                        <span>确 定</span>
                                    </div>
                                    :
                                    <div className="whiteBg1_child4_child3 publicbtn" 
                                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                        onClick={()=>{
                                            if(this.state.contact_info.length>=1){
                                                fetch(`${api}/Replace_payment/saveContact?user_id=${user_id}&nick_name=${nick_name}&contact_type=${this.state.contact_type}&contact_info=${this.state.contact_info}&client=${client}&proxy_id=${proxy_id}&proxy_name=${proxy_name}&from_brand=${from_brand}&action=edit`,{
                                                    method:"GET"
                                                }).then((data)=>data.json())
                                                .then((responseJson)=>{
                                                    this.setState({
                                                        statusInfo:responseJson,
                                                    })
                                                    // eslint-disable-next-line
                                                    if(responseJson.status!=0){
                                                        this.setState({
                                                            isAlert:true,
                                                            msg:"修改失败！"
                                                        })
                                                    }else{
                                                        this.setState({
                                                            statusInfo:responseJson,
                                                            isAlert:true,
                                                            msg:"修改成功！"
                                                        })
                                                    }
                                                })
                                                .catch((error) => {
                                                    alert(error)
                                                })
                                                // eslint-disable-next-line
                                            } else if(contact_info==''){
                                                this.setState({
                                                    isAlert:true,
                                                    msg:"提交信息不能为空！"
                                                })
                                            }
                                            }}
                                        
                                        >
                                        <span>修 改</span>
                                    </div>
                            }
                            </div>
                            <div className="whiteBg1_child5 WordArt" >详情:</div>
                            <div className="whiteBg1_child6 publicinput">
                                <textarea maxLength="90"
                                    onChange={(e)=>{
                                    }}
                                    onClick={()=>{
                                        this.setState({
                                            inputAlert:true,
                                            inputkeys:'details'
                                        })
                                    }}
                                    value={details}
                                ></textarea>
                            </div>
                            {/* 编辑详情 */}
                            <div className="whiteBg1_child7 publicbtn"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                onClick={()=>{
                                    // eslint-disable-next-line
                                    if(this.state.contact_info==""){
                                        this.setState({
                                            isAlert:true,
                                            msg:"基本信息不能为空!"
                                        })
                                    // eslint-disable-next-line
                                    } else if(this.state.details==''){
                                        this.setState({
                                            isAlert:true,
                                            msg:"编辑内容不能为空！"
                                        })
                                    }else{
                                        fetch(`${api}/replace_payment/saveDetails?user_id=${user_id}&details=${this.state.details}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line
                                            if(responseJson.status!=0){
                                                this.setState({
                                                    isAlert:true,
                                                    msg:responseJson.msg
                                                })
                                            }else{
                                                this.setState({
                                                    saveDetailsResults:responseJson,
                                                    isAlert:true,
                                                    msg:responseJson.msg
                                                })
                                            }
                                            
                                        })
                                        .catch((error) => {
                                            alert(error)
                                        })
                                    }
                                }}
                            >
                                <span>编 辑</span>
                            </div>
                        </div>
                        <div className="whiteBg2" >
                            <div>
                                <div className="WordArt" >买分比例:</div>
                                <div className='publicinput'>
                                    <input type="text"  placeholder="百分比"
                                        onChange={(e)=>{
                                        }}
                                        onClick={()=>{
                                            this.setState({
                                                inputAlert:true,
                                                inputkeys:'proportion'
                                            })
                                        }}
                                        value={this.state.proportion}
                                    />
                                     <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                                        onClick={()=>{
                                            this.setState({
                                                proportion :''
                                            })
                                        }} 
                                    ></i>
                                </div>
                                <div>比例越低，在等待队列排名越前(最大{max})</div>
                            </div>
                            <div>
                                <div className="WordArt">买分金额:</div>
                                <div className="publicinput">
                                    <input type="text" style={{top:"31%"}} 
                                        onChange={(e)=>{ 
                                        }}
                                        onClick={()=>{
                                            this.setState({
                                                inputAlert:true,
                                                inputkeys:'buyAmount'
                                            })
                                        }}
                                        placeholder="输入金额"
                                        value={this.state.buyAmount}
                                    />
                                    <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                                        onClick={()=>{
                                            this.setState({
                                                buyAmount :''
                                            })
                                        }} 
                                    ></i>
                                </div>
                                <div>实际得到分数：<span>{this.state.proportion*this.state.buyAmount/100}</span></div>
                            </div>
                            <div></div>
                        {/* 提交订单 */}
                            <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                onClick={()=>{
                                    // 返回orderStatus
                                  // eslint-disable-next-line 
                                    if(showInfo==""){
                                        this.setState({
                                            isAlert:true,
                                            msg:'请先提交基本资料！'
                                        })
                                        // eslint-disable-next-line
                                    }else if(this.state.proportion==""&&this.state.buyAmount==""){
                                        this.setState({
                                            isAlert:true,
                                            msg:'买份比例买分金额不能为空！'
                                        })
                                    }else if(this.state.proportion>max){
                                        this.setState({
                                            isAlert:true,
                                            msg:'大于系统最大买分比例！'
                                        })
                                    }else if(this.state.buyAmount<min_amount){
                                        this.setState({
                                            isAlert:true,
                                            msg:`小于系统最小买分金额！(${min_amount})`
                                        })
                                    }else if(this.state.buyAmount>max_amount){
                                        this.setState({
                                            isAlert:true,
                                            msg:`大于系统单笔最大买分金额！(${max_amount})`
                                        })
                                    }else{
                                        fetch(`${api}/Payment/transferPayment?user_id=${user_id}&nick_name=${nick_name}&fraction_scale=${this.state.proportion}&amount=${this.state.buyAmount}&client=${client}&proxy_id=${proxy_id}&proxy_name=${proxy_name}&from_brand=${from_brand}&order_type=1`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line 
                                            if(responseJson.status!=0){
                                                this.setState({
                                                    isAlert:true,
                                                    msg:responseJson.msg+"请输入其他金额"
                                                })
                                            }
                                            this.setState({
                                                orderStatus:responseJson,
                                                show:true,
                                            })
                                        })
                                        .catch((error) =>{
                                            alert(error)
                                        })
                                    }
                                }}
                                className="publicbtn"
                            >
                                <span>提 交</span>
                            </div>
                        </div>
                </div>
                {/* 提交结果弹窗 */}
                {
                    !this.state.isAlert?null:<div className="Alert-container" id="Alert-container"   
                        style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                        >
                        <div className="Alert-child-container" >
                            <div className="Alert-child">
                                {this.state.msg}     
                            </div>
                            <div className="Alert-btn">
                                    <div
                                        onClick={()=>{
                                        this.setState({
                                            isAlert:false
                                        })
                                        fetch(`${api}/replace_payment/register?user_id=${this.props.data.user_id}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line
                                            if(responseJson.status==0){
                                                this.setState({
                                                    results:responseJson,
                                                    contact_info:responseJson.data.contact_info,
                                                    // eslint-disable-next-line
                                                    contact_type:responseJson.data.contact_type == 0? '1':responseJson.data.contact_type,
                                                    details:responseJson.data.details,
                                                })
                                            }
                                        })
                                        .catch((error) => {
                                            alert(error)
                                        })
                                        }}
                                    >
                                        确定
                                    </div>
                                </div>
                            </div>
                        </div>
                }
                {/* 订单弹窗 */}
                {
                    // eslint-disable-next-line 
                    !this.state.show || orderStatus.status !=0?null : <div className="Applyfor-container" id="Applyfor-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    >
                    <div className="Applyfor-child-container">
                        <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                        <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                            <span>创建订单</span>
                        </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        show:false
                                    })
                                }}
                        ></div>
                        <div className="Applyfor-child">
                                <div>
                                    <span>充值方式：银行卡转账</span>
                                    <span>收款银行：{orderStatus.data.bank_name}</span>
                                </div>
                                <div>用户昵称:{nick_name}</div>
                                <div>收款账号：{orderStatus.data.card_num}
                                            <CopyToClipboard text={orderStatus.data.card_num}
                                                onCopy={this.onCopy.bind(this)}
                                            >
                                            <div
                                                id="Orderfuzhi1"
                                                className="publicbtn"
                                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                onTouchStart={()=>{
                                                    document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                }}
                                                onTouchEnd={()=>{
                                                    document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                }}
                                            >
                                            复制
                                            </div>
                                            </CopyToClipboard> 
                                </div>
                                <div>收款人姓名：{orderStatus.data.card_name}
                                            
                                            <CopyToClipboard text={orderStatus.data.card_name}
                                                onCopy={this.onCopy.bind(this)}
                                            ><div
                                                id="Orderfuzhi2"
                                                className="publicbtn"
                                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                onTouchStart={()=>{
                                                    document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                }}
                                                onTouchEnd={()=>{
                                                    document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                }}
                                                >
                                                复制
                                            </div>
                                            </CopyToClipboard> 
                                </div>
                                <div>转账金额:{Number(orderStatus.data.amount)}
                                        <CopyToClipboard text={Number(orderStatus.data.amount)}
                                                onCopy={this.onCopy.bind(this)}
                                            >
                                            <div
                                                id="Orderfuzhi3"
                                                className="publicbtn"
                                                style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                onTouchStart={()=>{
                                                    document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                }}
                                                onTouchEnd={()=>{
                                                    document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                }}
                                            >
                                            复制
                                            </div>
                                        </CopyToClipboard> 
                                </div>
                                <div>请务必按照该金额（包含小数点）进行转账，否则无法正常上分</div>
                        </div>
                        <div className="Applyfor-child2">
                            <div>注意事项:</div>
                            <div>1.付款账户为随机账号，请以最新账号为准</div>
                            <div>2.请在30分钟内完成支付，超时系统将不予处理</div>
                            <div>3.完成转账后请在60分钟后查询您的账号余额，若未到账请联系在线客服</div>
                        </div>
                        <div className="Applyfor-btn1">
                                <div id="Applyfor_confrim"  
                                    className="publicbtn"
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                    onClick={()=>{
                                        this.hideOrder();
                                        this.setState({
                                            showOrder3:true
                                        })
                                    }}
                                >
                                    <span>如何充值</span>
                                </div>
                        </div>
                        <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                        </div>
                    </div> 
                }
                {/* 复制结果弹窗 */}
                {
                    !this.state.isAlertFZ?null:<div className="Alert-container" id="Alert-container"   
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2),zIndex:'400'}}
                    >
                    <div className="Alert-child-container"  style={{height:'20%',top:'45%'}}>
                        <div className="Alert-child" style={{position:'absolute',top:'0',width:'90%',height:"65%"}}>
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn" style={{position:'absolute',bottom:'5%',left:'35%',width:'30%',height:"30%"}}>
                                <div style={{width:'100%',height:"100%",float:'left',background:'rgba(0, 152, 255, 255)'}} onClick={()=>{
                                    this.setState({
                                        isAlertFZ:false
                                    })
                                }}>确定</div>
                                
                            </div>
                        </div>
                    </div>
                }
                {/* 如何充值 */}
                {
                // eslint-disable-next-line 
                !this.state.showOrder3 ?null : <div className="Applyfor-container" id="Applyfor-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                >
                <div className="Applyfor-child-container" >
                    <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                        <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                            <span>如何充值</span>
                        </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        showOrder3:false
                                    })
                                }}
                    ></div>
                    <div className="CZZY-child">
                        <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/zhiyin.jpg)"}}></div>
                    </div>
                   
                    <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                    </div>
                </div> 
                 }
                {/* input弹窗 */}
                {
                !this.state.inputAlert?null:<InputAlert 
                    setinputAlert={this.setinputAlert.bind(this)}
                    setPropsMoney={this.setPropsMoney.bind(this)}
                    // eslint-disable-next-line
                    money={this.state.inputkeys=='contact_info'?this.state.contact_info:(this.state.inputkeys=='details'?this.state.details:(this.state.inputkeys=='proportion'?this.state.proportion:this.state.buyAmount))}
                    height={this.props.height}
                    // eslint-disable-next-line
                    maxLength={this.state.inputkeys=='contact_info'?'15':(this.state.inputkeys=='details'?'100':(this.state.inputkeys=='proportion'?'3':'6'))}
                    ></InputAlert>
                }
        </Recharge>
    }
}
