import React from "react";
import Recharge  from "../../container/Recharge";
import "./Zfb.less"; 
import './ZfbToBank.less';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import InputAlert from "../Alert/InputAlert";
const api="http://111.231.57.144:8081/api"
export default class ZfbToBank extends React.Component{
    constructor(props){
        super(props);
        this.state={
            //充值金额
            money:'',
            min_amount:"50",
            max_amount:"5000",
            isAlert:false,
            paymentResults:'',
            show:false,
            orderResults:''
        }
        fetch(`${api}/payment/guduPayPaymentIndex`,{
            method:"GET"
        }).then((data)=>data.json())
        .then((responseJson)=>{
            // eslint-disable-next-line
           if(responseJson.status==0){
                this.setState({
                    results:responseJson
                })
           }else{
                this.setState({
                    results:false,
                })
           }
        })
        .catch((error) =>{
            alert(error)
        })
    }
    setPath(val){
        this.props.setProps(val)
    }
    //避免异步请求结果未返回就切换页面报错
    componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    onCopy(copy){
        this.setState({
            isAlertFZ:true,
            msg:"复制成功！ "+copy
        })
    }
    setinputAlert(val){
        this.setState({
            inputAlert:val
        })
    }
    setPropsMoney(val){
        this.setState({
            money:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
        })
    }
    render(){ 
        const{user_id,nick_name,client,proxy_id,proxy_name,from_brand} =this.props.data;
        const height =this.props.height;
        const {results,orderResults} = this.state;
        if(results){
            var min_amount = results.data.gudu_pay_min_amount;
            var max_amount = results.data.gudu_pay_max_amount;
            var dataList = results.data.random_amount;
        }else{
            // eslint-disable-next-line
            var min_amount ='';
            // eslint-disable-next-line
            var max_amount ="";
            // eslint-disable-next-line
            var dataList= [];
        }
        return  <Recharge  current="3" setPath={this.setPath.bind(this)}>
                    <div className="ZfbToBankBox1">
                        <div  className="ZfbToBankBox1_child1" >
                            <p className="WordArt">充值金额:</p>
                            <div  className="publicinput">
                                <input  type="text" placeholder="请输入充值金额"
                                    autoComplete="off"
                                    onChange={()=>{

                                    }}
                                    onClick={()=>{
                                        this.setState({
                                            inputAlert:true,
                                        })
                                    }}
                                    value={this.state.money}
                                />
                                {/* 点击清空金额 */}
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)"}}
                                    onClick={()=>{
                                        this.setState({
                                            money:''
                                        })
                                    }}
                                        
                                ></i>
                            </div>
                            <span>（充值范围：{min_amount}-{max_amount}）</span>
                        </div>
                        <div className="ZfbToBankBox1_child2 WordArt"  
                        >
                        常用金额:
                        </div>
                    </div>
                    <div className="ZfbToBankBox2"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/neikuagn.png)"}}>
                        
                        <div className="ZfbToBankBox2_child1">
                        {
                            dataList.map((item,index)=>{
                                //  eslint-disable-next-line 
                                return <p style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/19.png)"}} 
                                                    key={index}
                                                    id={`p${index}`}
                                                    onTouchStart={()=>{
                                                        this.setState({
                                                            money:Number(this.state.money)+Number(item)
                                                        })
                                                        document.getElementById(`p${index}`).style.backgroundImage=`url(${process.env.PUBLIC_URL}/images/22.png)`
                                                    }}
                                                    onTouchEnd={()=>{
                                                        document.getElementById(`p${index}`).style.backgroundImage=`url(${process.env.PUBLIC_URL}/images/19.png)`
                                                    }}
                                                >
                                            {/*eslint-disable-next-line */}
                                            <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/yb"+(index+1)+".png)"}} className={index+1==3?'ZfbToBankBox2_child1_child3':''}></i>
                                            <span>+{item}</span> 
                                </p>
                            })
                        }
                        </div>
                    </div>
                    <div className="ZfbToBankBox3 publicbtn" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                         onClick={()=>{
                             // eslint-disable-next-line 
                             if(this.state.money.indexOf('.')=='1'){
                                 this.setState({
                                     msg:"充值金额不能是小数!",
                                     isAlert:true
                                 })

                             }else if(this.state.money<= Number(this.state.min_amount)||this.state.money>=Number(this.state.max_amount)){
                                this.setState({
                                    msg:"充值金额需符合充值范围！",
                                    isAlert:true,
                                })
                             }else{
                                fetch(`${api}/Payment/transferPayment?user_id=${user_id}&nick_name=${nick_name}&amount=${this.state.money}&client=${client}&proxy_id=${proxy_id}&proxy_name=${proxy_name}&from_brand=${from_brand}&order_type=0`,{
                                    method:"GET"
                                }).then((data)=>data.json())
                                .then((responseJson)=>{
                                    // eslint-disable-next-line 
                                    if(responseJson.status!=0){
                                        this.setState({
                                            isAlert:true,
                                            msg:responseJson.msg+"请输入其他金额"
                                        })
                                    }
                                    this.setState({
                                        orderResults:responseJson,
                                        show:true,
                                    })
                                })
                                .catch((error) =>{
                                    alert(error)
                                })
                             }
                           
                        }}
                    >
                        <span>确认充值</span>
                    </div> 
                    {/* 弹窗 */}
                    {
                        !this.state.isAlert?null:<div className="Alert-container" id="Alert-container"  
                        style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                        >
                        <div className="Alert-child-container" >
                            <div className="Alert-child">
                                {this.state.msg}   
                            </div>
                            <div className="Alert-btn">
                                    <div
                                        onClick={()=>{
                                            this.setState({
                                                isAlert:false
                                            })
                                        }}
                                    >
                                        确定
                                    </div>
                                </div>
                            </div>
                        </div>
                    }
                     {/* 订单弹窗 */}
             {
                            // eslint-disable-next-line 
                            !this.state.show ?null : <div className="Applyfor-container" id="Applyfor-container" 
                            style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                            >
                            <div className="Applyfor-child-container">
                                <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                                <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                                    <span>创建订单</span>
                                </div>
                                </div>
                                <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                        onTouchEnd={()=>{
                                            this.setState({
                                                show:false
                                            })
                                        }}
                                ></div>
                                <div className="Applyfor-child">
                                        <div>
                                            <span>充值方式：支付宝转账</span>
                                            <span>收款银行：{orderResults.data.bank_name}</span>
                                        </div>
                                        <div>用户昵称:{nick_name}</div>
                                        <div>收款账号：{orderResults.data.card_num}
                                                    <CopyToClipboard text={orderResults.data.card_num}
                                                        onCopy={this.onCopy.bind(this)}
                                                    >
                                                    <div
                                                        id="Orderfuzhi1"
                                                        className="publicbtn"
                                                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                        onTouchStart={()=>{
                                                            document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                        }}
                                                        onTouchEnd={()=>{
                                                            document.getElementById("Orderfuzhi1").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                        }}
                                                    >
                                                    复制
                                                    </div>
                                                    </CopyToClipboard> 
                                        </div>
                                        <div>收款人姓名：{orderResults.data.card_name}
                                                    
                                                    <CopyToClipboard text={orderResults.data.card_name}
                                                        onCopy={this.onCopy.bind(this)}
                                                    ><div
                                                        id="Orderfuzhi2"
                                                        className="publicbtn"
                                                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                        onTouchStart={()=>{
                                                            document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                        }}
                                                        onTouchEnd={()=>{
                                                            document.getElementById("Orderfuzhi2").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                        }}
                                                        >
                                                        复制
                                                    </div>
                                                    </CopyToClipboard> 
                                        </div>
                                        <div>转账金额:{Number(orderResults.data.amount)}
                                                <CopyToClipboard text={Number(orderResults.data.amount)}
                                                        onCopy={this.onCopy.bind(this)}
                                                    >
                                                    <div
                                                        id="Orderfuzhi3"
                                                        className="publicbtn"
                                                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/5copy.png)"}}
                                                        onTouchStart={()=>{
                                                            document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5.png)';
                                                        }}
                                                        onTouchEnd={()=>{
                                                            document.getElementById("Orderfuzhi3").style.backgroundImage='url('+process.env.PUBLIC_URL+'/images/5copy.png)';
                                                        }}
                                                    >
                                                    复制
                                                    </div>
                                                </CopyToClipboard> 
                                        </div>
                                        <div>请务必按照该金额（包含小数点）进行转账，否则无法正常上分</div>
                                </div>
                                <div className="Applyfor-child2">
                                    <div>注意事项:</div>
                                    <div>1.付款账户为随机账号，请以最新账号为准</div>
                                    <div>2.请在30分钟内完成支付，超时系统将不予处理</div>
                                    <div>3.完成转账后请在60分钟后查询您的账号余额，若未到账请联系在线客服</div>
                                </div>
                                <div className="Applyfor-btn1">
                                        <div id="Applyfor_confrim"  
                                            className="publicbtn"
                                            style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                                            onClick={()=>{
                                                this.setState({
                                                    show:false,
                                                    showOrder3:true
                                                })
                                            }}
                                        >
                                            <span>如何充值</span>
                                        </div>
                                </div>
                                <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                                </div>
                            </div> 
                        }
                {/* 如何充值 */}
                {
                // eslint-disable-next-line 
                !this.state.showOrder3 ?null : <div className="Applyfor-container" id="Applyfor-container" 
                style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                >
                <div className="Applyfor-child-container" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/kuangd2.png)"}}>
                    <div className="Applyfor-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                        <div  className="Applyfor-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                            <span>如何充值</span>
                        </div>
                        </div>
                        <div className="Applyfor-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        showOrder3:false
                                    })
                                }}
                    ></div>
                    <div className="CZZY-child">
                        <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/zhiyin.jpg)"}}></div>
                    </div>
                   
                    <div className="Applyfor-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)"}}></div>
                    </div>
                </div> 
                }
                {/* 复制结果弹窗 */}
                {
                    !this.state.isAlertFZ?null:<div className="Alert-container" id="Alert-container" 
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2),zIndex:'400'}}
                    >
                    <div className="Alert-child-container"  style={{height:'20%',top:'45%'}}>
                        <div className="Alert-child" style={{position:'absolute',top:'0',width:'90%',height:"65%"}}>
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn" style={{position:'absolute',bottom:'5%',left:'35%',width:'30%',height:"30%"}}>
                                <div style={{width:'100%',height:"100%",float:'left',background:'rgba(0, 152, 255, 255)'}} onClick={()=>{
                                    this.setState({
                                        isAlertFZ:false
                                    })
                                }}>确定</div>
                                
                            </div>
                        </div>
                    </div>
                }
                {/* input弹窗 */}
                {
                !this.state.inputAlert?null:<InputAlert 
                    setinputAlert={this.setinputAlert.bind(this)}
                    setPropsMoney={this.setPropsMoney.bind(this)}
                    money={this.state.money}
                    height={this.props.height}
                    maxLength='6'
                    ></InputAlert>
                }
        </Recharge> 
    }
}