import React from "react";
import WithDrawCash from "../../container/WithDrawCash";
import './Present.less';
import InputAlert from '../Alert/InputAlert'
// const api="http://111.231.57.144:8081/api";
const api="http://10.63.252.30:8087/api";
export default class Present_withdrawCash extends React.Component{
    constructor(props){
        super(props);
        this.state={ 
            money:'',
            presentID:''
        }
    }  
    setPath(val){
        this.props.setProps(val)
    }
     //避免异步请求结果未返回就切换页面报错
     componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    setinputAlert(val){
        this.setState({
            inputAlert:val
        })
    }
    setPropsMoney(val){
        if(this.state.inputkeys==='presentID'){
            this.setState({
                presentID:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        }else if(this.state.inputkeys==='money'){
            this.setState({
                money:val.match(/\d+(\.\d)?/) ? val.match(/\d+(\.\d)?/)[0] : ''
            })
        }
    }
    render(){
         //定义变量，固定弹窗的高度
         const height =this.props.height;
        const {user_id,nick_name} = this.props.data;
        var min_amount =50;
        var max_amount = 10000;
        return <WithDrawCash current="2" setPath={this.setPath.bind(this)} >
                 <div className="ZfbBox1">
                 <div  className="ZfbBox1_child2" style={{top:'20%'}} >
                            <p className="WordArt">赠送ID:</p>
                            <div  className="publicinput">
                                <input type="text" placeholder="输入ID"
                                    autoComplete="off"
                                    onClick={()=>{
                                        this.setState({
                                            inputAlert:true,
                                            inputkeys:'presentID'
                                        })
                                    }}
                                    onChange={(e)=>{
                                    }}
                                    value={this.state.presentID}
                                />
                                {/* 点击清空金额 */}
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)"}}
                                    onClick={()=>{
                                        this.setState({
                                            presentID:''
                                        })
                                    }}
                                ></i>
                            </div>
                        </div>
                        <div  className="ZfbBox1_child2" >
                            <p className="WordArt">赠送金额:</p>
                            <div  className="publicinput">
                                <input id="userInput" type="text" placeholder="请输入赠送金额"
                                    autoComplete="off"
                                    onChange={(e)=>{
                                    }}
                                    onClick={()=>{
                                        this.setState({
                                            inputAlert:true,
                                            inputkeys:'money'
                                        })
                                    }}
                                    value={this.state.money}
                                />
                                {/* 点击清空金额 */}
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)"}}
                                    onClick={()=>{
                                        this.setState({
                                            money:''
                                        })
                                    }}
                                        
                                ></i>
                            </div>
                            <span>（赠送范围：{min_amount}-{max_amount}）</span>
                        </div>
                    </div>
                    <div className="ZfbBox2"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/12.png)",paddingTop:"2%",paddingLeft:'3%',color:'rgba(111, 73, 52, 255)'}}>
                        注意：赠送成功后，系统默认延时半小时发送赠送金额，您也可以到赠送记录里面再次确认，立即发送金额。如有疑问，请及时联系客服
                    </div>
                    <div className="DhBox3">
                        <p 
                            onClick={()=>{
                                this.props.setProps("#/withdrawCash/presenthistory")
                            }}
                        >赠送记录</p>
                        <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                            className="publicbtn"
                            onClick={()=>{
                                // eslint-disable-next-line 
                                if(this.state.money==""){
                                    this.setState({
                                        isAlert:true,
                                        msg:'金额不能为空'
                                    })
                                }else if(this.state.money>max_amount || this.state.money<min_amount){
                                    this.setState({
                                        isAlert:true,
                                        msg:'金额超出赠送范围！'
                                    })
                                    // eslint-disable-next-line
                                }else if(this.state.money%50!=0){
                                    this.setState({
                                        isAlert:true,
                                        msg:'赠送金额必须是50的倍数'
                                    })
                                }else {
                                    fetch(`${api}/give/searchbyuser?by_id=${this.state.presentID}`,{
                                        method:"GET"
                                    }).then((data)=>data.json())
                                    .then((responseJson)=>{
                                        // eslint-disable-next-line
                                        if(responseJson.status==0){
                                            this.setState({
                                                indexResults:responseJson,
                                                msg2:`确认给游戏id(${responseJson.data._id}), 游戏昵称(${responseJson.data.game_nick})的玩家赠送${this.state.money}吗？`
                                            })
                                        }else{
                                            this.setState({
                                                msg2:responseJson.msg.substring(0,20)
                                            })
                                        }
                                    })
                                    .catch((error) => {
                                        alert(error)
                                    })
                                    this.setState({
                                        msg:"",
                                        isAlert2:true,
                                    })
                                }
                            }}
                        >申请赠送</div>
                    </div> 
                     {/* 返回昵称弹窗 */}
             {
                    !this.state.isAlert2?null:<div className="MyorderAlert-container" id="MyorderAlert-container"   
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    >
                    <div className="MyorderAlert-child-container"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/alertbg.jpg)",top:'30%'}} >
                        <div className="MyorderAlert-child" >
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg2}     
                        </div>
                        <div className="MyorderAlert-btn" >
                                <div 
                                    className='publicbtn'
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                                    onClick={()=>{
                                    this.setState({
                                        isAlert2:false,
                                        msg:''
                                    })
                                }}>否</div>
                                <div
                                    className='publicbtn'
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                                    onClick={()=>{
                                        //发送上分请求
                                        if(this.state.indexResults){
                                            fetch(`${api}/give/giveAmount?by_id=${this.state.presentID}&user_id=${user_id}&user_name=${nick_name}&by_name=${this.state.indexResults.data.game_nick}&amount=${this.state.money}`,{
                                                method:"GET"
                                            }).then((data)=>data.json())
                                            .then((responseJson)=>{
                                                // eslint-disable-next-line
                                                if(responseJson.status==0){
                                                    this.setState({
                                                        upScorestatus:responseJson,
                                                        isAlert:true,
                                                        msg:"赠送成功!"
                                                    })
                                                }else{
                                                    this.setState({
                                                        upScorestatus:responseJson,
                                                        isAlert:true,
                                                        msg:responseJson.msg
                                                    })
                                                }
                                                this.setState({
                                                    isAlert2:false
                                                })
                                            })
                                            .catch((error) =>{
                                                alert(error)
                                            })
                                        } 
                                    }}
                                >
                                    是
                                </div>
                            </div>
                        </div>
                    </div>
                }
            {/* 提示信息弹窗 */}
            {
            !this.state.isAlert
                ?null
                :<div className="Alert-container" id="Alert-container"  
                    style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)+'px'}}
                >
                <div className="Alert-child-container" >
                    <div className="Alert-child">
                    {/* eslint-disable-next-line  */}
                        { this.state.msg}     
                    </div>
                    <div className="Alert-btn">
                            <div
                                onClick={()=>{
                                    this.setState({
                                        isAlert:false,
                                        presentID:'',
                                        money:''
                                    })
                                }}
                            >
                                确定
                            </div>
                        </div>
                    </div>
                </div>
            }
            {/* input弹窗 */}
            {
                !this.state.inputAlert?null:<InputAlert 
                    setinputAlert={this.setinputAlert.bind(this)}
                    setPropsMoney={this.setPropsMoney.bind(this)}
                    money={this.state.inputkeys==='presentID'?this.state.presentID:this.state.money}
                    height={this.props.height}
                    maxLength={this.state.inputkeys==='presentID'?'10':'6'}
                    ></InputAlert>
            }
        </WithDrawCash>
    } 
}
